import React, { Component } from 'react';
import '../assets/css/UserPage/userPage.css'
import { connect } from 'react-redux';
import '../assets/css/LoggedinUserPage/loggedinUserPage.css'
import { loadUsers } from '../actions/UserActions';
import { loadPosts } from '../actions/PostActions';
import { loadFriends, loadSelectedFriend } from '../actions/FriendActions.js';

class PostPage extends Component {
    state = {
        postsCount: 0,
        followersCount: 0,
        posts: {}
    }


    componentDidMount() {
        this.setState({ posts: this.props.location.state.posts })
        this.props.loadPosts()
        this.props.loadFriends()
        this.countPosts(this.props.location.state.posts)
        this.countFollowers(this.props.location.state.users)
    }

    countPosts = (posts) => {
        let postsCount = 0
        posts.forEach((post) => {
            if (post.owner._id === this.props.location.state.selectedFriend._id) {
                postsCount++
            }
        });

        let result = ''
        if (postsCount > 1) {
            result = postsCount + ' posts';
        } else if (postsCount === 0) { result = 'no posts' }
        else { result = '1 post'; }
        this.setState({ postsCount: result })
    }
    countFollowers = (users) => {
        let followersCount = 0
        console.log('users: ', users)
        users.forEach((user) => {
            console.log('followers: ', user.followers)
            user.followers.forEach((followed) => {
                console.log('followed: ', followed)
                // if (user._id === followed._id) {
                //     followersCount++
                // }
            })
        });

        // let result = ''
        // if (followersCount > 1) {
        //     result = followersCount + ' followers';
        // } else if (followersCount === 0) { result = 'no followers' }
        // else { result = '1 follower'; }
        // this.setState({ followersCount: result })
    }

    render() {
        const { selectedFriend } = this.props.location.state
        return (<div className="user-page-container">

            <div className="user-info flex col">
                <div className="image-username-container flex row">
                    <img className="user-img" alt="user pic" src={selectedFriend.imgUrl}></img>
                    <div className="user-socual-info-container flex col">
                        <span className="username">{selectedFriend.username}</span>
                        <div className="socual-info-section flex row">
                            <span className="user-info-field username">{this.state.postsCount}</span>
                            <span className="user-info-field username">folowers: </span>
                            <span className="user-info-field username">folowing: </span>
                        </div>
                    </div>
                </div>

            </div>

        </div>
        )







        // (<div className="user-page-container">
        //     user...
        //     <span className="-user-nickname">{user.nickName}</span>
        // </div>)
    }
}

const mapStateToProps = state => {
    return {
        posts: state.post.posts,
        friends: state.friend.friends,
        selectedFriend: state.friend.selectedFriend,
        users: state.user.users

    };
};
const mapDispatchToProps = {
    loadPosts,
    loadFriends, loadSelectedFriend,
    loadUsers
};

export default connect(mapStateToProps, mapDispatchToProps)(PostPage);